@import "../styles/variables.scss";
@import "../styles/breakpoints.scss";

.create-listing {
    background-color: rgb(242, 242, 242);
    padding: 40px 60px 120px;

    @include mm {
        padding: 40px 20px;
    }

    h1 {
        color: $green;

        @include sm {
            font-size: 22px;
            padding: 0 10px;
        }
    }

    &_step1 {
        background-color: white;
        padding: 30px 40px;
        border-radius: 20px;
        margin-top: 40px;

        @include sm {
            margin-top: 20px;
            padding: 30px 20px;
        }

        h2 {
            color: $orange;
        }

        hr {
            margin: 15px 0 25px;
        }

        h3 {
            margin: 40px 0 20px;
            color: $blue;
        }

        .category-list {
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            gap: 8px;
            padding: 0 10px;

            @include lg {
                padding: 0 10px;
            }

            @include md {
                padding: 0;
            }

            .category {
                display: flex;
                background-color: $green;
                flex-direction: column;
                padding: 5px 5px;
                justify-content: center;
                align-items: center;
                width: 100px;
                height: 70px;
                cursor: pointer;
                transition: background-color 0.2s ease, color 0.2s ease;

                @include sm {
                    width: 80px;
                    height: 56px;
                }

                &_icon,
                &_label {
                    transition: background-color 0.2s ease, color 0.2s ease;
                }

                &_icon {
                    font-size: 18px;
                    color: white;

                    @include sm {
                        font-size: 16px;
                    }
                }

                &_label {
                    font-weight: 400;
                    font-size: 12px;
                    text-align: center;
                    color: white;

                    @include sm {
                        font-size: 10px;
                    }
                }

                p {
                    font-weight: 400;
                    font-size: 12px;
                    text-align: center;
                    color: white;
                }

                &:hover {
                    background-color: white;

                    .category_icon,
                    .category_label {
                        background-color: none;
                        color: $green;
                    }
                }
            }

            .selected {
                border: 2px solid $orange;
                background-color: white;

                .category_icon,
                .category_label {
                    background-color: white;
                    color: $green;
                }
            }
        }

        .type-list {
            display: flex;
            flex-direction: column;
            gap: 20px;

            .type {
                display: flex;
                justify-content: space-between;
                align-items: center;
                max-width: 600px;
                padding: 15px 30px;
                border: 1px solid $lightgrey;
                box-sizing: border-box;
                cursor: pointer;
                transition: 0.3s ease;
                outline: 0;

                &_text {
                    max-width: 400px;

                    h4 {
                        color: $green;
                    }

                    span {
                        font-size: 14px;
                        color: $darkgrey;
                    }
                }

                &_icon {
                    font-size: 30px;
                    color: $green;
                }

                &:hover {
                    border: 1px solid $orange;
                    background-color: $lightergrey;
                }
            }

            .selected {
                border: 1px solid $orange;
                background-color: none;
            }
        }

        .full {
            max-width: 700px;
        }

        .half {
            max-width: 700px;
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 40px;

            @include ml {
                display: flex;
                flex-direction: column;
                gap: 0;
            }
        }

        .location {
            p {
                font-weight: 700;
                margin: 20px 0 10px;
                color: $green;
            }

            input {
                border: 1px solid $lightgrey;
                padding: 15px 30px;
                font-size: 14px;
                font-weight: 400;
                width: 100%;

                &:focus {
                    outline: none;
                }

                &::placeholder {
                    color: rgb(189, 187, 187);
                }

            }
        }

        .basics {
            display: flex;
            flex-wrap: wrap;
            gap: 40px;

            @include sm {
                gap: 14px;
            }

            .basic {
                display: flex;
                align-items: center;
                gap: 30px;
                padding: 15px;
                border: 1px solid $lightgrey;

                @include sm {
                    gap: 14px;
                    padding: 10px;
                }

                span {
                    font-weight: 400;
                    color: $green;

                    @include sm {
                        font-size: 14px;
                    }
                }


                &_count {
                    display: flex;
                    align-items: center;
                    gap: 9px;
                    font-size: 15px;
                }
            }
        }
    }

    &_step2 {
        background-color: white;
        padding: 30px 40px;
        border-radius: 20px;
        margin-top: 40px;

        @include sm {
            padding: 30px 20px;
        }

        h2 {
            color: $orange;
        }

        hr {
            margin: 15px 0 25px;
        }

        h3 {
            margin: 40px 0 20px;
            color: $blue;
        }

        .amenities {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            gap: 8px;
            padding: 0 10px;

            .facility {
                display: flex;
                background-color: $green;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                padding: 5px 5px;
                width: 100px;
                height: 70px;
                cursor: pointer;
                transition: background-color 0.2s ease, color 0.2s ease;

                @include sm {
                    width: 80px;
                    height: 60px;
                }

                &_icon,
                &_label {
                    transition: background-color 0.2s ease, color 0.2s ease;
                }

                &_icon {
                    font-size: 18px;
                    color: white;

                    @include sm {
                        font-size: 16px;
                    }
                }

                &_label {
                    font-weight: 400;
                    font-size: 12px;
                    text-align: center;
                    color: white;


                    @include sm {
                        font-size: 10px;
                    }
                }

                &:hover {
                    background-color: white;

                    .facility_icon,
                    .facility_label {
                        background-color: none;
                        color: $green;
                    }
                }
            }

            .selected {
                border: 2px solid $orange;
                background-color: white;

                .facility_icon,
                p {
                    background-color: white;
                    color: $green;
                }
            }
        }

        .photos {
            display: flex;
            flex-wrap: wrap;
            gap: 15px;

            .photo {
                position: relative;
                width: 250px;
                height: 150px;
                cursor: move;

                img {
                    width: 100%;
                    height: 100%;
                }

                button {
                    position: absolute;
                    right: 0;
                    top: 0;
                    padding: 7px 5px 3px 5px;
                    border: none;
                    background-color: rgba(255, 255, 255, 0.4);
                    font-size: 20px;
                    cursor: pointer;

                    &:hover {
                        color: white;
                    }
                }
            }

            label {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                cursor: pointer;
                border: 1px dashed $grey;

                .icon {
                    font-size: 60px;
                    color: $green;
                }

                p {
                    font-weight: 400;
                    text-align: center;
                    color: $green;
                }
            }

            .together {
                width: 250px;
                height: 150px;
            }

            .alone {
                padding: 40px 100px;
                border-radius: 10px;

                @include mm {
                    padding: 40px 80px;
                }

                @include sm {
                    padding: 40px 30px;
                }
            }
        }

        .title {
            input {
                border: 1px solid $lightgrey;
                padding: 15px 30px;
                font-size: 16px;
                font-weight: 500;
                width: 100%;

                &:focus {
                    outline: none;
                }
            }
        }

        .description {
            p {
                font-weight: 700;
                margin: 20px 0 10px;
                color: $green;
            }

            input,
            textarea {
                border: 1px solid $lightgrey;
                padding: 15px 30px;
                font-size: 14px;
                font-weight: 400;
                width: 600px;

                @include ml {
                    width: 450px;
                }

                @include md {
                    width: 350px;
                }

                @include sm {
                    width: 280px;
                }

                &::placeholder {
                    color: rgb(189, 187, 187);
                }

                &:focus {
                    outline: none;
                }
            }

            span {
                font-size: 25px;
                font-weight: 700;
                margin-right: 20px;
            }

            .price {
                width: 200px;
            }
        }
    }

    .submit_btn {
        margin-top: 40px;

        @include buttonStyle;

        &:hover {
            background: none;
            border: 1px solid $lightorange;
            box-sizing: border-box;
            color: $lightorange;
        }
    }
}